<template>
    <div>
        <i @click="likeClick()" class="fa fa-heart like-icon"></i>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    data(){
        return {
            like: {
                users: [],
                count: 0,
            }
        }
    },
    async mounted(){
        let userInformation = JSON.parse(localStorage.getItem("userInformation"));
        await axios.get('/api/likePostWho', {
            params: {post_id: this.$route.params.post}
        }).then(res => {
            console.log(res.data);
            if(res.data === 'failure'){
                this.like.count = 0;
            }else{
                for(let i = 0; i < res.data.length; i++){
                    this.like.users.push(res.data[i].nickname);
                    if(res.data[i].consumer_id === this.$store.state.login.consumer_id){
                        document.querySelector('.likeAndShareBtn').style.backgroundColor = 'lightgrey';
                        document.querySelector('.likeAndShareBtn').style.color = 'grey';
                    }
                }
                this.like.count = this.like.users.length;
            }
        }).catch(err => {
            console.log(err);
        })
    },
    methods: {
        likeWho(){
            axios.get('/api/likePostWho', {
                params: {post_id: this.$route.params.post}
            }).then(res => {
                for(let i = 0; i < res.data.length; i++){
                    this.like.users.push(res.data[i].nickname);
                    if(res.data[i].consumer_id === this.$store.state.login.user.consumer_id){
                        document.querySelector('.likeAndShareBtn').style.backgroundColor = 'lightgrey';
                        document.querySelector('.likeAndShareBtn').style.color = 'grey';
                    }
                }
                this.like.users = this.like.users.filter(user => user !== undefined);
                this.like.count = this.like.users.length;
            }).catch(err => {
                console.log(err);
            })
        },
        likeClick(){
            let userInformation = JSON.parse(localStorage.getItem("userInformation"));
            // 좋아요한 사람들의 배열에 로그인한 닉네임이 포함되어있지 않은 경우 플러스
            if(!this.like.users.includes(this.$store.state.login.user.consumer_id)){
                axios.post('/api/likesPlus', {
                    post_id: this.$route.params.post,
                    consumer_id: userInformation.nickname
                }).then(res => {
                    console.log(res);
                })
            // 반대일 때 마이너스
            }else{
                axios.delete('/api/likesMinus', {
                    params: {
                        post_id: this.$route.params.post,
                        customer_id: this.$store.state.login.user.customer_id
                    }
                }).then(res => {
                    console.log(res);
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.like-icon{
    font-size: 25px;
    color: rgb(225, 225, 225);
}
</style>